@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  background-color: black;
  height: 100vh;
  padding-bottom: 40px;
}



p {
  color: white;
}

h1 {
  color: #d2b48c;
}

h2 {
  color: white;
}

h3 {
  color: white;
}

h4 {
  color: white;
}

h5 {
  color: #d2b48c;
}

.nav {
  display: flex;
  justify-content: space-around;
  padding-bottom: 40px;
}

.nav a {
  cursor: pointer;
  font-family: "Courier New";
  font-weight: bolder;
  padding: 8px;
}

.nav a:hover {
  background-color: #c7ea46;
  color: black;
  border-radius: 10px;
}

.top a {
  text-decoration: none;
  color: #c7ea46;
}

.header {
  -webkit-text-decoration: underline #c7ea46;
          text-decoration: underline #c7ea46;
  font-size: 3rem;
}

/* html,
body {
  height: 100%;
} */

p {
  font-family: "Roboto Mono", monospace;
}

.title {
  color: #d2b48c;
  text-align: left;
}

.artist {
  text-align: left;
}

.big-song-container {
  display: flex;
  justify-content: space-between;
  background-color: #222222;
  padding: 10px 40px 10px 40px;
  width: 50vw;
  margin: 0 auto;
  margin-bottom: 10px;
}

.song-container-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.song-container-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
}



.heart {
  cursor:pointer;
  font-size: 30px;
  margin-bottom: 5px;
}

.heart:hover {
  color: #c7ea46;
}

.add-song {
  padding-top: 20px;
}

form {
  display: flex;
  flex-direction: column;
  width: 60vw;
  margin: 0 auto;
  margin-bottom: 40px;
  padding-top: 20px;
}

input {
  width: 40vw;
}


form,
input {
  padding-bottom: 5px;
  line-height: 20px;
  text-transform: uppercase;
  font-family: "Roboto Mono", monospace;
  color: #222222;
  font-weight: bolder;
}
.add_song_button {
  background-color: black;
  border-color: black;
  color: #c7ea46;
  font-size: 16px;
  font-weight: bolder;
  font-family: "Roboto Mono", monospace;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 10px;
  width: 20vw;
  cursor: pointer;
  padding: 5px;
}

.add_song_button:hover {
  background-color: #c7ea46;
  border-color: #c7ea46;
  color: black;
}

.label {
  color: white;
}

#input-container {
  display: flex;
  justify-content: space-between;
}
h2 {
  font-family: "Roboto Mono", monospace;
  padding-left: 20px;
  font-weight: bolder;
}

.discover {
    background-color: black;
}

.tile {
    width: 80vw;
    margin: 0 auto;
    margin-bottom: 100px;
}
